// -----------------------------------------------------------------------------

// Carousel fade
.carousel-fade{
  .carousel-inner{
    .item{
      -webkit-transition-property: opacity;
      transition-property: opacity;
      // transition-delay: 0.6s;
    }

    .item,
    .active.left,
    .active.right{
      opacity: 0;
    }

    .active,
    .next.left,
    .prev.right{
      opacity: 1;
    }

    .next,
    .prev,
    .active.left,
    .active.right{
      left: 0;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0);
    }
  }
}
// End Carousel fade

// Custom carousel
.custom-carousel{
  background-color: $body-color;

  .indicators-container{
    width: 100%;
    position: absolute;
    left: 0px;
    right: 0px;
    bottom: 10px;
    top: auto;
    margin: 0 auto;

    .carousel-indicators{
      text-align: center;
      width: auto;
      margin: 0px 15px;
      bottom: 0px;

      // Align indicators (Right and Left = 0 to Centrate)
      left: 0px;
      right: 0px;
      // End Align indicators (Right and Left = 0 to Centrate)

      li{
        background-color: rgba(0,0,0,0);
        border: 1px solid $primary-color;
        width: 15px;
        height: 15px;
        margin: 0px;
        border-radius: 100%;

        // Space between indicators
        margin: 0px 5px;
        // End Space between indicators

        -webkit-transition: all 0.3s ease;
        -webkit-transition: all 0.3s ease;
        transition: all 0.3s ease;

        @include on-event{
          background-color: rgba($primary-color,0.5);
        }
      }

      .active{
        @include on-event(true){
          background-color: $primary-color;
          border: 1px solid $primary-color;
          width: 15px;
          height: 15px;
          border-radius: 100%;
        }
      }
    }
  }

  // Align content vertically
